import React, { useState, useMemo, useRef, useEffect } from 'react';
import Card from '../components/ui/Card';
import Button from '../components/ui/Button';
import { SearchIcon, PlusIcon, PlayIcon, EditIcon, TrashIcon, DuplicateIcon, ArchiveIcon, UploadIcon, DotsVerticalIcon, LightbulbIcon, ClockIcon, RefreshIcon, LockClosedIcon } from '../assets/icons';

const formatDate = (dateString) => {
    if (!dateString) return null;
    return new Date(dateString).toLocaleString('az-AZ', { day: '2-digit', month: '2-digit', year: 'numeric', hour: '2-digit', minute: '2-digit' });
};

const getQuizStatus = (quiz) => {
    const now = new Date();
    const startTime = quiz.start_time ? new Date(quiz.start_time) : null;
    const endTime = quiz.end_time ? new Date(quiz.end_time) : null;

    if (startTime && now < startTime) {
        return { text: ` balayr ${formatDate(startTime)}`, color: 'blue', active: false };
    }
    if (endTime && now > endTime) {
        return { text: 'Bitib', color: 'red', active: false };
    }
    if (startTime && endTime) {
        return { text: ` bitir ${formatDate(endTime)}`, color: 'green', active: true };
    }
    if (startTime) {
        return { text: 'Aktiv', color: 'green', active: true };
    }
    return { text: 'Aktiv', color: 'green', active: true };
};

const QuizCard = ({ quiz, onStartQuiz, onCloneQuiz, onEditQuiz, onArchiveRequest, onDeleteRequest, isAdmin }) => {
    const [menuOpen, setMenuOpen] = useState(false);
    const menuRef = useRef(null);

    useEffect(() => {
        const handleClickOutside = (event) => {
            if (menuRef.current && !menuRef.current.contains(event.target)) {
                setMenuOpen(false);
            }
        };
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, [menuRef]);

    const questions = quiz.questions || [];
    const status = getQuizStatus(quiz);

    return (
        <Card className={`flex flex-col transition-transform duration-200 ${quiz.isArchived ? 'bg-gray-100' : 'hover:shadow-orange-200 hover:-translate-y-1'}`}>
            <div className="flex-grow">
                <div className="flex flex-col sm:flex-row justify-between items-start mb-2">
                    <h2 className="text-xl font-bold text-gray-800 flex-1 pr-2 mb-2 sm:mb-0">{quiz.title}</h2>
                    {quiz.isArchived ? (
                        <span className="text-xs font-semibold uppercase tracking-wider text-gray-600 bg-gray-200 px-2 py-1 rounded-full whitespace-nowrap">ARXVD</span>
                    ) : (
                        <span className="text-xs font-semibold uppercase tracking-wider text-orange-600 bg-orange-100 px-2 py-1 rounded-full whitespace-nowrap">{quiz.category || 'Kateqoriyasz'}</span>
                    )}
                </div>
                <p className="text-gray-600 mb-4 h-12 overflow-hidden text-sm">{quiz.description}</p>
                <div className="flex justify-between items-center text-sm text-gray-500 mb-4">
                    <span>{questions.length} sual</span>
                    <div className="flex items-center gap-3">
                        {quiz.passcode && (
                            <span className="flex items-center gap-1 font-medium text-gray-600" title="Giri kodu tlb olunur">
                                <LockClosedIcon className="h-4 w-4" />
                            </span>
                        )}
                        {quiz.attempt_limit > 0 && (
                            <span className="flex items-center gap-1 font-medium text-purple-600" title={`Maksimum ${quiz.attempt_limit} chd`}>
                                <RefreshIcon className="h-4 w-4" />
                                {quiz.attempt_limit} chd
                            </span>
                        )}
                        <span className={`flex items-center gap-1 font-medium text-${status.color}-600`}>
                            <ClockIcon className="h-4 w-4" />
                            {status.text}
                        </span>
                    </div>
                </div>
            </div>
            <div className="flex items-stretch gap-2 mt-auto">
                <Button onClick={() => onStartQuiz(quiz.id)} className="flex-1" disabled={questions.length === 0 || quiz.isArchived || !status.active}><PlayIcon /> <span className="hidden sm:inline ml-1">Bala</span></Button>
                {isAdmin && (
                    <div className="relative" ref={menuRef}>
                        <Button onClick={() => setMenuOpen(!menuOpen)} variant="secondary" className="h-full"><DotsVerticalIcon /></Button>
                        {menuOpen && (
                            <div className="absolute right-0 bottom-full mb-2 w-48 bg-white rounded-md shadow-lg z-10 border border-gray-200">
                                <button onClick={() => { onCloneQuiz(quiz.id); setMenuOpen(false); }} disabled={quiz.isArchived} className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center disabled:opacity-50"><DuplicateIcon /> <span className="ml-2">Kopyala</span></button>
                                <button onClick={() => { onEditQuiz(quiz.id); setMenuOpen(false); }} disabled={quiz.isArchived} className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center disabled:opacity-50"><EditIcon /> <span className="ml-2">Redakt et</span></button>
                                <button onClick={() => { onArchiveRequest(quiz.id, !quiz.isArchived); setMenuOpen(false); }} className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center"><ArchiveIcon /> <span className="ml-2">{quiz.isArchived ? 'Arxivdn xar' : 'Arxiv sal'}</span></button>
                                <div className="border-t border-gray-100 my-1"></div>
                                <button onClick={() => { onDeleteRequest(quiz.id); setMenuOpen(false); }} className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 flex items-center"><TrashIcon /> <span className="ml-2">Sil</span></button>
                            </div>
                        )}
                    </div>
                )}
            </div>
        </Card>
    );
};

const QuizListPage = ({ quizzes, onStartQuiz, onAddNewQuiz, onEditQuiz, onDeleteRequest, onCloneQuiz, onArchiveRequest, onStartSmartPractice, showArchived, setShowArchived, isAdmin }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [sortBy, setSortBy] = useState('date_desc');
    const [selectedCategory, setSelectedCategory] = useState('all');

    const categories = useMemo(() => ['all', ...new Set(quizzes.filter(q => !q.isArchived).map(q => q.category || 'Kateqoriyasz'))], [quizzes]);

    const sortedAndFilteredQuizzes = useMemo(() => {
        const term = searchTerm.toLowerCase();
        return quizzes
            .filter(quiz =>
                (quiz.isArchived || false) === showArchived &&
                (selectedCategory === 'all' || (quiz.category || 'Kateqoriyasz') === selectedCategory) &&
                (quiz.title.toLowerCase().includes(term) ||
                    (quiz.description && quiz.description.toLowerCase().includes(term)))
            )
            .sort((a, b) => {
                switch (sortBy) {
                    case 'title_asc': return a.title.localeCompare(b.title);
                    case 'title_desc': return b.title.localeCompare(a.title);
                    case 'questions_asc': return (a.questions || []).length - (b.questions || []).length;
                    case 'questions_desc': return (b.questions || []).length - (a.questions || []).length;
                    case 'date_asc': return a.id - b.id;
                    case 'date_desc':
                    default: return b.id - a.id;
                }
            });
    }, [quizzes, searchTerm, sortBy, selectedCategory, showArchived]);

    return (
        <div className="animate-fade-in">
            <Card className="mb-8 !p-4 bg-gray-50 border border-gray-200">
                <div className="flex flex-col md:flex-row justify-between items-center gap-4">
                    <div className="relative flex-grow w-full">
                        <span className="absolute inset-y-0 left-0 flex items-center pl-3"><SearchIcon /></span>
                        <input type="text" placeholder="Adna v ya tsvirin gr axtar..." value={searchTerm} onChange={e => setSearchTerm(e.target.value)} className="pl-10 pr-4 py-2 border-2 border-gray-200 rounded-lg w-full focus:ring-orange-400 focus:border-orange-400 transition" />
                    </div>
                    <div className="flex flex-col sm:flex-row items-center gap-2 w-full md:w-auto">
                        {!showArchived && (
                            <select value={selectedCategory} onChange={e => setSelectedCategory(e.target.value)} className="py-2 px-4 border-2 border-gray-200 rounded-lg bg-white w-full sm:w-auto">
                                {categories.map(cat => (
                                    <option key={cat} value={cat}>{cat === 'all' ? 'Btn kateqoriyalar' : cat}</option>
                                ))}
                            </select>
                        )}
                        <select value={sortBy} onChange={e => setSortBy(e.target.value)} className="py-2 px-4 border-2 border-gray-200 rounded-lg bg-white w-full sm:w-auto">
                            <option value="date_desc">n yenilr</option>
                            <option value="date_asc">n khnlr</option>
                            <option value="title_asc">Ad (A-Z)</option>
                            <option value="title_desc">Ad (Z-A)</option>
                            <option value="questions_desc">ox sual</option>
                            <option value="questions_asc">Az sual</option>
                        </select>
                        <Button onClick={onStartSmartPractice} variant="primary" className="w-full sm:w-auto"><LightbulbIcon /><span className="hidden sm:inline ml-2">All Mq</span></Button>
                        {isAdmin && <Button onClick={() => setShowArchived(!showArchived)} variant="secondary" className="w-full sm:w-auto">{showArchived ? 'Aktiv Testlr' : 'Arxiv'}</Button>}
                        {isAdmin && <Button onClick={onAddNewQuiz} className="w-full sm:w-auto"><PlusIcon /><span className="sm:hidden md:inline">Test yarat</span></Button>}
                    </div>
                </div>
            </Card>

            {sortedAndFilteredQuizzes.length === 0 ? (
                <Card className="text-center py-12">
                    <p className="text-gray-500">
                        {showArchived ? 'Arxivd test taplmad.' : 'Testlr taplmad. Filtlri dyimy v ya yeni test yaratmaa chd edin!'}
                    </p>
                </Card>
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {sortedAndFilteredQuizzes.map(quiz => (
                        <QuizCard
                            key={quiz.id}
                            quiz={quiz}
                            onStartQuiz={onStartQuiz}
                            onCloneQuiz={onCloneQuiz}
                            onEditQuiz={onEditQuiz}
                            onArchiveRequest={onArchiveRequest}
                            onDeleteRequest={onDeleteRequest}
                            isAdmin={isAdmin}
                        />
                    ))}
                </div>
            )}
        </div>
    );
};

export default QuizListPage;