import React, { useState, useEffect } from 'react';
import Button from '../components/ui/Button';
import Card from '../components/ui/Card';
import QuestionEditor from '../components/QuestionEditor';
import ComboBox from '../components/ui/ComboBox';
import { ArrowLeftIcon, PlusIcon, CheckIcon, UploadIcon, LibraryIcon, LockClosedIcon } from '../assets/icons';

const QuizEditorPage = ({ quiz, onSave, onBack, showToast, existingCategories = [], onImportRequest, onAddFromBankRequest, onDraftChange }) => {

    const handleQuizInfoChange = (e) => {
        const { name, value, type, checked } = e.target;
        let finalValue = type === 'checkbox' ? checked : value;
        if (type === 'datetime-local') {
            finalValue = value ? new Date(value).toISOString() : null;
        }
        onDraftChange({ ...quiz, [name]: finalValue });
    };

    const handleCategoryChange = (value) => {
        onDraftChange({ ...quiz, category: value });
    };

    const handleQuestionsChange = (newQuestions) => {
        onDraftChange({ ...quiz, questions: newQuestions });
    };

    const addQuestion = () => {
        const newQuestion = { 
            id: Date.now(), 
            text: '', 
            type: 'single', 
            options: ['', ''], 
            correctAnswers: [], 
            correctAnswer: true, 
            orderItems: ['', ''],
            imageUrl: '',
            explanation: '',
            points: 1
        };
        handleQuestionsChange([...quiz.questions, newQuestion]);
    };

    const updateQuestion = (updatedQuestion) => {
        handleQuestionsChange(quiz.questions.map(q => q.id === updatedQuestion.id ? updatedQuestion : q));
    };

    const deleteQuestion = (questionId) => {
        handleQuestionsChange(quiz.questions.filter(q => q.id !== questionId));
    };
    
    const handleDuplicateQuestion = (questionId) => {
        const questionToClone = quiz.questions.find(q => q.id === questionId);
        if (!questionToClone) return;

        const clonedQuestion = { ...questionToClone, id: Date.now() + Math.random() };
        const originalIndex = quiz.questions.findIndex(q => q.id === questionId);
        const newQuestions = [...quiz.questions];
        newQuestions.splice(originalIndex + 1, 0, clonedQuestion);
        handleQuestionsChange(newQuestions);
    };

    const handleSave = () => {
        if (!quiz.title || !quiz.title.trim()) {
            showToast("Testin ad bo ola bilmz!");
            return;
        }
        if (!quiz.category || !quiz.category.trim()) {
            showToast("Kateqoriya bo ola bilmz!");
            return;
        }
        onSave(quiz);
    };

    const formatDateTimeForInput = (isoString) => {
        if (!isoString) return '';
        const date = new Date(isoString);
        return new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toISOString().slice(0, 16);
    };

    if (!quiz) {
        return <div>Yklnir...</div>;
    }

    return (
        <div className="animate-fade-in space-y-6">
            <div className="flex flex-col sm:flex-row justify-between sm:items-center gap-4">
                <div>
                    <Button onClick={onBack} variant="secondary" className="mb-2 sm:mb-0"><ArrowLeftIcon /><span className="hidden sm:inline">Siyahya qayt</span></Button>
                    <h1 className="text-2xl sm:text-3xl font-bold text-gray-800 mt-2">Test Redaktoru</h1>
                </div>
                <div className="w-full sm:w-auto">
                    <Button onClick={handleSave} className="w-full"><CheckIcon />Testi yadda saxla</Button>
                </div>
            </div>

            <Card>
                <h3 className="text-lg font-bold text-gray-800 mb-4 border-b pb-2">sas Mlumatlar</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-x-8 gap-y-6">
                    <div className="space-y-4 col-span-1">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">Testin ad</label>
                            <input type="text" name="title" value={quiz.title || ''} onChange={handleQuizInfoChange} className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">Tsvir</label>
                            <textarea name="description" value={quiz.description || ''} onChange={handleQuizInfoChange} rows="5" className="mt-1 block w-full rounded-md border-gray-300 shadow-sm"></textarea>
                        </div>
                    </div>
                    <div className="space-y-4 col-span-1">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">Kateqoriya</label>
                            <ComboBox options={existingCategories} value={quiz.category || ''} onChange={handleCategoryChange} placeholder="Kateqoriyan sein v ya yazn..." />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-1">Test n vaxt (dqiq)</label>
                            <input type="number" name="timeLimit" value={quiz.timeLimit || 10} onChange={handleQuizInfoChange} min="1" className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" />
                        </div>
                        <div>
                            <h4 className="text-sm font-medium text-gray-700 mb-3">Parametrlr</h4>
                            <div className="space-y-3 pt-2 border-t">
                                <label className="flex items-center cursor-pointer"><input type="checkbox" name="shuffleQuestions" checked={!!quiz.shuffleQuestions} onChange={handleQuizInfoChange} className="h-4 w-4 text-orange-600 rounded border-gray-300" /> <span className="ml-2 text-sm text-gray-700">Suallar qardr</span></label>
                                <label className="flex items-center cursor-pointer"><input type="checkbox" name="shuffleOptions" checked={!!quiz.shuffleOptions} onChange={handleQuizInfoChange} className="h-4 w-4 text-orange-600 rounded border-gray-300" /> <span className="ml-2 text-sm text-gray-700">Variantlar qardr</span></label>
                            </div>
                        </div>
                    </div>
                </div>
            </Card>

            <Card>
                <h3 className="text-lg font-bold text-gray-800 mb-4 border-b pb-2">Qabaqcl Tnzimlmlr</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-x-8 gap-y-6">
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Balama vaxt (Knll)</label>
                        <input type="datetime-local" name="start_time" value={formatDateTimeForInput(quiz.start_time)} onChange={handleQuizInfoChange} className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" />
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Bitm vaxt (Knll)</label>
                        <input type="datetime-local" name="end_time" value={formatDateTimeForInput(quiz.end_time)} onChange={handleQuizInfoChange} className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" />
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Chd limiti (0 = limitsiz)</label>
                        <input type="number" name="attempt_limit" value={quiz.attempt_limit || 0} onChange={handleQuizInfoChange} min="0" className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" />
                    </div>
                     <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Giri Kodu (Knll)</label>
                        <input type="text" name="passcode" value={quiz.passcode || ''} onChange={handleQuizInfoChange} className="mt-1 block w-full rounded-md border-gray-300 shadow-sm" placeholder="Test n parol tyin et" />
                    </div>
                </div>
            </Card>

            <div>
                <div className="flex flex-wrap gap-4 justify-between items-center mb-4">
                    <h2 className="text-xl font-bold text-gray-800">Suallar</h2>
                    <div className="flex flex-wrap gap-2">
                        <Button onClick={onAddFromBankRequest} variant="secondary"><LibraryIcon />Bankdan lav et</Button>
                        <Button onClick={onImportRequest} variant="secondary"><UploadIcon />CSV-dn idxal et</Button>
                        <Button onClick={addQuestion}><PlusIcon />Sual lav et</Button>
                    </div>
                </div>
                <div className="space-y-4">
                    {quiz.questions.map((q, index) => <QuestionEditor key={q.id} question={q} index={index} onUpdate={updateQuestion} onDelete={() => deleteQuestion(q.id)} onDuplicate={() => handleDuplicateQuestion(q.id)} />)}
                </div>
                 {quiz.questions.length === 0 && (
                    <Card className="text-center py-12 border-dashed border-2 border-gray-300">
                        <p className="text-gray-500">Hl he bir sual lav edilmyib.</p>
                        <Button onClick={addQuestion} className="mt-4"><PlusIcon />lk sual lav et</Button>
                    </Card>
                )}
            </div>
        </div>
    );
};

export default QuizEditorPage;